import React, { useState, useEffect } from "react";
import { MaskedInput } from "grommet";
import { format, parse } from "date-fns";

const daysInMonth = (month) => new Date(2019, month, 0).getDate();

const DateTimePicker = ({ value, onChange }) => {
  const [date, setDate] = useState(format(value, "yyyy-MM-dd"));
  const [time, setTime] = useState(format(value, "HH:mm"));

  useEffect(() => {
    const obj = parse(`${date} ${time}`, "yyyy-MM-dd HH:mm", new Date());
    if (obj && !isNaN(obj.getTime())) {
      onChange && onChange(obj);
    }
  }, [date, time]);

  return (
    <>
      {JSON.stringify()}
      <MaskedInput
        mask={[
          {
            length: 4,
            options: Array.from({ length: 100 }, (v, k) => 2019 + k),
            regexp: /^[1-2]$|^19$|^20$|^19[0-9]$|^20[0-9]$|^19[0-9][0-9]$|^20[0-9][0-9]$/,
            placeholder: "yyyy",
          },
          { fixed: "-" },
          {
            length: 2,
            options: Array.from({ length: 12 }, (v, k) =>
              `${k + 1}`.padStart(2, 0)
            ),
            regexp: /^1[0,1-2]$|^0?[1-9]$|^0$/,
            placeholder: "mm",
          },
          { fixed: "-" },
          {
            length: 2,
            options: Array.from(
              {
                length: daysInMonth(parseInt(date.split("/")[0], 10)),
              },
              (v, k) => `${k + 1}`.padStart(2, 0)
            ),
            regexp: /^[1-2][0-9]$|^3[0-1]$|^0?[1-9]$|^0$/,
            placeholder: "dd",
          },
        ]}
        value={date}
        onChange={(event) => setDate(event.target.value)}
      />
      <MaskedInput
        mask={[
          {
            length: 2,
            options: Array.from({ length: 24 }, (v, k) =>
              `${k}`.padStart(2, 0)
            ),
            regexp: /^1[0,1-2]$|^0?[1-9]$|^0$/,
            placeholder: "hh",
          },
          { fixed: ":" },
          {
            length: 2,
            options: Array.from({ length: 60 }, (v, k) =>
              `${k}`.padStart(2, 0)
            ),
            regexp: /^[0-9][0-9]$|^[0-9]$/,
            placeholder: "mm",
          },
        ]}
        value={time}
        onChange={(event) => setTime(event.target.value)}
      />
    </>
  );
};

export default DateTimePicker;
